import React, { useEffect, useState } from 'react'
import './style.css'
import * as Audio_icon_ON from '../../../../assets/Clean Walk/Audio_icon_ON.png'
import * as Audio_icon from '../../../../assets/Clean Walk/Audio_icon.png'
import * as Vibration_Icon_ON from '../../../../assets/Clean Walk/Vibration Icon_ON.png'
import * as Vibration_Icon from '../../../../assets/Clean Walk/Vibration Icon.png'
import * as kannada from '../../../../assets/kannlan.png'
import McInput from '../../../Common/McInput'
import { useLanguageStore } from '../../../../store/LanguageStore'
import { userstore } from '../../../../store/userStore'
import { instancestore } from '../../../../store/InstanceStore'
import { useHistory } from 'react-router-dom'
import { useActivePopup } from '../../../../store/ActivePopup';
import { useColourObjectStore } from '../../../../store/ColourStore'

const useAudio = url => {

  const [audio] = useState(new Audio(url));
  const [playing, setPlaying] = useState(false);
  const toggle = () => setPlaying(!playing);

  useEffect(() => {
    playing ? audio.play() : audio.pause();
  },
    [playing]
  );

  useEffect(() => {
    audio.addEventListener('ended', () => setPlaying(false));
    return () => {
      audio.removeEventListener('ended', () => setPlaying(false));
    };
  }, []);
  return [playing, toggle];
};


const Settings = ({ }) => {

  const { colourObj } = useColourObjectStore();


  const { setActivePopup } = useActivePopup();

  const { selfuser, setselfuser, editprofileuser } = userstore();

  const [playing, toggle] = useAudio("");
  const [vib, setvib] = useState(false)

  const { Language, setLanguage } = useLanguageStore();

  const [instanceid, setinstanceid] = useState('');
  const { instance } = instancestore();
  const history = useHistory()


  return (
    <div className="settings_listitem_container">

      <div className="settings_listitem">
        <div className="card1">
          <img className="profilelogo" style={{ borderColor: colourObj.color_m2 }} src={selfuser.photo_url} alt="John" />
          <div className="profilename">{selfuser.firstname + " " + selfuser.lastname}</div>
          <div className="profileemail">{selfuser.email}</div>
          <div className="editprofile"><button onClick={() => {
            setActivePopup("editprofile")
          }}>{(Language?.value === "KANNADA") ? "ನಿಮ್ಮ ಪ್ರೊಫೈಲ್ ಸಂಪಾದಿಸಿ" : "EDIT YOUR PROFILE"}</button></div>
        </div>
      </div>

      <div onClick={() => {
        setvib(!vib)
        window.navigator.vibrate([100, 30, 100, 30, 100, 30, 200,
          30, 200, 30, 200, 30, 100, 30, 100, 30, 100])
      }}
        className="settings_listitem">
        {vib ? <img className='settings_buttons' src={Vibration_Icon_ON} /> : <img className='settings_buttons' src={Vibration_Icon} />}
        <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
          {(Language?.value === "KANNADA") ? "ಫೋನ್ ವೈಬ್ರೇಶನ್" : "VIBRATION"}</div>

      </div>

      <div onClick={() => { toggle }} className="settings_listitem">
        {playing ? <img className='settings_buttons' src={Audio_icon_ON} /> : <img className='settings_buttons' src={Audio_icon} />}
        <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
          {(Language?.value === "KANNADA") ? "ಆಡಿಯೋ ಬಟನ್" : "AUDIO"}</div>
      </div>

      <div onClick={() => {
      }} className="settings_listitem">
        <img className='settings_buttons' src={kannada} />
        <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
          <div className="inputfield_sub_container">
            <McInput
              type="picker"
              id="input_i_bodytypetextbox"
              name={`LANGUAGE`}
              required={true} ENGLISH
              value={Language?.value}
              onchange={(data: any) => {
                setLanguage(data)
              }}
              options={[
                { "key": "ENGLISH", "value": "ENGLISH" },
                { "key": "KANNADA", "value": "KANNADA" },
              ]}
              description={"Select your LANGUAGE"}
            />
          </div></div>
      </div>

      <div onClick={() => {
      }} className="settings_listitem">
        <img className='settings_buttons' src={kannada} />
        <div className="settings_listtext shadow" style={{ backgroundColor: colourObj.color_s2 }}>
          <div className="inputfield_sub_container">
            <McInput
              type="picker"
              id="input_i_instance_textbox"
              name={`GAME`}
              required={true}
              value={instanceid?.value}
              onchange={(data: any) => {
                setinstanceid(data)
                history.push(`/${data.key}/`)
                window.location.reload();
              }}
              options={(instance) && instance.map(
                (obj: any) => {
                  return { "key": obj.id, "value": obj.i_title }
                }
              )}
              description={"Select your game."}
            />
          </div></div>

      </div>

    </div>
  )
}

export default Settings