import React, { useEffect, useState } from 'react'
import './style.css'
import McInput from '../../../Common/McInput'
import { userstore } from '../../../../store/userStore'

const EditProfile = (props:any) => {

  const { selfuser, setselfuser, editprofileuser } = userstore();

  const [firstname, setfirstname] = useState(selfuser.firstname);
  const [lastname, setlastname] = useState(selfuser.lastname);
  const [email, setemail] = useState(selfuser.email);
  const [image, setimage] = useState(selfuser.image);
  const [photo_url, setphoto_url] = useState(selfuser.photo_url);
  const [dob, setdob] = useState(selfuser.dob);
  const [gender, setgender] = useState(selfuser.gender);
  const [address, setaddress] = useState(selfuser.address);
  const [dial_code, setdial_code] = useState(selfuser.dial_code);
  const [phone, setphone] = useState(selfuser.phone);

  useEffect(() => {
    let data = {
      firstname: String(document.getElementById("input_firstnametextbox").value),
      lastname: String(document.getElementById("input_lastnametextbox").value),
      email: String(document.getElementById("input_emailtextbox").value),
      image: String(document.getElementById("input_imagetextbox").value),
      photo_url: String(document.getElementById("input_photo_urltextbox").value),
      dob: String(document.getElementById("input_dobtextbox").value),
      gender: gender?.value,
      address: String(document.getElementById("input_addresstextbox").value),
      dial_code: String(document.getElementById("input_dial_codetextbox").value),
      phone: String(document.getElementById("input_phonetextbox").value),
      is_deleted: false,
    }
  
    editprofileuser(data)
  }, [props.confirm1])

  return (
    <div className="main_input_wrap">

      <div className="div_column_wrap">

        <div className="inputfield_sub_container">
          <McInput
            label={"FIRST NAME"}
            id="input_firstnametextbox"
            name={`data.firstname`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={firstname}
            onchange={setfirstname}
            description={"Enter user Firstname"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            label={"LAST NAME"}
            id="input_lastnametextbox"
            name={`data.lastname`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={lastname}
            onchange={setlastname}
            description={"Enter user Lastname"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            label={"EMAIL"}
            id="input_emailtextbox"
            name={`data.email`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={email}
            onchange={setemail}
            description={"Enter user Email-ID"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            label={"IMAGE"}
            id="input_imagetextbox"
            name={`data.image`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={image}
            onchange={setimage}
            description={"Enter user Image"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            label={"PHOTO URL"}
            id="input_photo_urltextbox"
            name={`data.photo_url`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={photo_url}
            onchange={setphoto_url}
            description={"Enter user Photo URL"}
          />
        </div>

      </div>

      <div className="div_column_wrap">

        <div className="inputfield_sub_container">
          <McInput
            label={"DOB(yyy-mm-dd)"}
            id="input_dobtextbox"
            name={`data.dob`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={dob}
            onchange={setdob}
            description={"Enter user Date of birth"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            type="picker"
            id="input_gendertextbox"
            name={`GENDER`}
            required={true}
            value={gender?.value}
            onchange={setgender}
            options={[
              { "key": "0", "value": "MALE" },
              { "key": "1", "value": "FEMALE" },
              { "key": "2", "value": "OTHER" }
            ]}
            description={"Enter user Gender"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            label={"ADDRESS"}
            id="input_addresstextbox"
            name={`data.address`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={address}
            onchange={setaddress}
            description={"Enter user Address"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            label={"DIAL CODE"}
            id="input_dial_codetextbox"
            name={`data.dial_code`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={dial_code}
            onchange={setdial_code}
            description={"Enter user Dial Code"}
          />
        </div>

        <div className="inputfield_sub_container">
          <McInput
            label={"PHONE NUMBER"}
            id="input_phonetextbox"
            name={`data.phone`}
            inputtype="Text"
            type="text"
            min_length="3"
            required={true}
            value={phone}
            onchange={setphone}
            description={"Enter user Phone Number"}
          />
        </div>

      </div>
    </div>
  )
}

export default EditProfile